import ons from 'onsenui';
import {playSound} from '../audio';

window.SoundButtonV2 = {
    render: sound => {
        const base64Image = soundImages[sound.id + '.jpg'];

        return `
<div class="card">
    <div 
        class="cardImage" 
        style="background-image: url('data:image/jpeg;base64,${base64Image}');"
        onclick="SoundBoardV2.onClickTile('${sound.id}')"
    >
        <ons-ripple color="rgba(255,0,0,0.6)" background="rgba(0,0,0,0)"></ons-ripple>
    </div>
    <ons-button ripple modifier="large material" class="languageText" onclick="SoundBoardV2.onClickTile('${sound.id}-en-us')">${sound.languageText['en-us']}</ons-button>
    <ons-button ripple modifier="large material" class="languageText" onclick="SoundBoardV2.onClickTile('${sound.id}-zh-hk')">${sound.languageText['zh-hk']}</ons-button>
    <ons-button ripple modifier="large material" class="languageText" onclick="SoundBoardV2.onClickTile('${sound.id}-zh-cn')">${sound.languageText['zh-cn']}</ons-button>
</div> 
`;
    }
};

window.SoundBoardV2 = {
    render:      () => ({
        pageHTML: `
        <ons-page id="SoundBoardV2-page">
            <style>
                .card {
                    min-width: 300px; 
                    max-width: 480px; 
                    width: 40vw; 
                    display:inline-block; 
                    border-radius: 0.75em; 
                    overflow: hidden;            
                }
            
                .cardImage {
                    border-top-left-radius: 0.75em;
                    border-top-right-radius: 0.75em;
                    background-color: dimgray;
                    background-position: center center; 
                    background-size: cover;
                    background-repeat: no-repeat; 
                    height: 300px;
                }
                
                .languageText {
                    border-radius:0;
                    font-size:1.5em; 
                    padding:0.3em;
                }
                
                .languageText:last-child {
                    border-bottom-left-radius: 0.75em;
                    border-bottom-right-radius: 0.75em;
                }
                
            </style>
            <ons-toolbar>
              <div class="left">
                <ons-back-button>Back</ons-back-button>
              </div>
              <div class="center">
                ${activeSoundPack.name}
              </div>
            </ons-toolbar>
            <div style="text-align: center">
                ${activeSoundPack.contents.map(SoundButtonV2.render).join('')}    
            </div>
        </ons-page>
`
    }),
    onClickBack: () => ons.notification.alert('todo: navigate back'),
    onClickTile: id => playSound(id)
};