const ORIGIN    = 'https://bazaarworld.xyz';
const MARKET_ID = '4eecf1b2-1b65-417d-bc4b-46f6b57c9238';

export async function getFreeSoundPacks() {
    let res = await fetch(`${ORIGIN}/packages/list/${MARKET_ID}`, {
        mode: 'cors'
    });

    res = await res.json();

    return res.filter(p => p.is_free);
}

export async function getPackage(id) {
    const url = `${ORIGIN}/packages/download/${id}`;

    try {
        const cachedPackage = await APP_CACHE.match(url);

        if (cachedPackage) {
            return cachedPackage;
        } else {
            await APP_CACHE.add(url);
            return APP_CACHE.match(url);
        }
    } catch (e) {
        console.log(e);

        // Fallback to fetch without cache
        return await fetch(url);
    }
}