import ons from 'onsenui';
import {playSound} from '../audio';

window.SoundButtonV1 = {
    render: sound => `
            <tr>
                <td align="center" style="position:relative;width:300px;height:200px;background:url('data:image/jpeg;base64,${localStorage.getItem(sound.id + '.jpg')}'); background-size: cover;background-position: center center;">
                    <ons-button style="position:absolute; bottom:0; right:0;font-size:2em; padding:0.5em" modifier="cta" onclick="SoundBoardV1.onClickTile('${sound.id}')">!</ons-button>
                </td>
                <td align="center">
                    <ons-button style="font-size:2em; padding:0.5em" modifier="cta" onclick="SoundBoardV1.onClickTile('${sound.id}-en-us')">${sound.languageText['en-us']}</ons-button>
                </td>
                <td align="center">
                    <ons-button style="font-size:2em; padding:0.5em" modifier="cta" onclick="SoundBoardV1.onClickTile('${sound.id}-zh-hk')">${sound.languageText['zh-hk']}</ons-button>
                </td>
                <td align="center">
                    <ons-button style="font-size:2em; padding:0.5em" modifier="cta" onclick="SoundBoardV1.onClickTile('${sound.id}-zh-cn')">${sound.languageText['zh-cn']}</ons-button>
                </td>
            </tr>
`
};

window.SoundBoardV1 = {
    render:      () => ({
        pageHTML: `
        <ons-page id="soundboardv1-page">
            <ons-toolbar>
              <div class="center">
                ${activeSoundPack.name}
                <ons-button modifier="cta" style="position:absolute; top:50%; left: 4px;transform: translateY(-50%);" onclick="SoundBoardV1.onClickBack()">
                    &lt; Back
                </ons-button>
              </div>
            </ons-toolbar>
            <table width="100%">
                <tr><th>Sound</th><th>English</th><th>Cantonese</th><th>Mandarin</th></tr>
                ${activeSoundPack.contents.map(SoundButtonV1.render).join('')}    
            </table>
        </ons-page>
`
    }),
    onClickBack: () => ons.notification.alert('todo: navigate back'),
    onClickTile: id => playSound(id)
};