import 'onsenui';
import './templates';

const root = document.getElementById('rootNavigator');

export async function start() {
    try {
        caches.open('babbelboard')
            .then(cache => window.APP_CACHE = cache);
    } catch (e) {
        console.log(e);
    }

    if (MainMenu.load) {
        return MainMenu.load()
            .then(pageData => root.pushPage('MainMenu', MainMenu.render(pageData)));
    } else {
        return root.pushPage('MainMenu', MainMenu.render());
    }
}
