import JSZip from 'jszip';
import ons from 'onsenui';
import {getFreeSoundPacks, getPackage} from './api';
import {defineSound} from './audio';

import './SoundBoardV1';
import './SoundBoardV2';

const root = document.getElementById('rootNavigator');

window.soundImages = {};

async function loadPackFile(file) {
    const [filename, extension] = file.name.split('.');
    if (extension.toLowerCase() === 'mp3') {
        return defineSound({
            name:        filename,
            arrayBuffer: await file.async('arraybuffer')
        });
    } else if (extension.toLowerCase() === 'jpg') {
        // Fallback to memory storage
        soundImages[file.name] = await file.async('base64');
    } else if (file.name === 'package.json') {
        try {
            window.activeSoundPack = JSON.parse(await file.async('text'));
        } catch (e) {
            console.log('Could not parse the package metadata!');
        }
    }
}

window.SoundPackList = {
    render: list => list.map(item => `
                <ons-button onclick="MainMenu.onClickPack('${item.id}')" style="margin: 1em; padding:1em; width:20vw; max-width: 480px; min-width: 200px;">
                    ${item.name}
                </ons-button>`).join('')
};

window.MainMenu = {
    load:              getFreeSoundPacks,
    render:            (soundPacks) => ({
        pageHTML: `
        <ons-page id="mainmenu-page">
        <ons-splitter>
          <ons-splitter-side id="menu" side="left" width="220px" collapse swipeable>
            <ons-page>          
            <ons-list>
                <ons-list-item onclick="MainMenu.onClickBuy()" tappable>
                  Buy more sound packs
                </ons-list-item>
                <ons-list-item onclick="MainMenu.onClickClearCache()" tappable>
                  Clear app cache
                </ons-list-item>
            </ons-list>
            </ons-page>
          </ons-splitter-side>
        <ons-splitter-content>
            <ons-page>
                <style>
                    #loader {
                        position: absolute;
                        top: 0;
                        left: 0;
                        right: 0;
                        bottom: 0;
                        display: none;
                        background: rgba(255,255,255,0.4);
                    }
                    
                    #loader .spinner {
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        width: 10vw;
                        height: 10vw;
                    }
                    
                    .buttonMenu {
                        font-size: 1em;
                    }
                </style>
                <ons-toolbar>
                  <div class="left">
                    <ons-toolbar-button onclick="MainMenu.onClickMenuButton()">
                        <i class="zmdi zmdi-menu"></i>
                    </ons-toolbar-button>
                  </div>
                  <div class="center">
                    BabbleBoard
                  </div>
                </ons-toolbar>
                <div id="mainmenu-page-list">
                    ${SoundPackList.render(soundPacks)}    
                </div>
                <div id="loader">
                    <ons-progress-circular class="spinner" indeterminate></ons-progress-circular>
                </div>
            </ons-page>
        </ons-splitter-content>
        </ons-splitter>
        </ons-page>        
`
    }),
    onClickMenuButton: () => {
        document.getElementById('menu').open();
    },
    onClickClearCache: async () => {
        const keys = await caches.keys();
        await Promise.all(keys.map(k => caches.delete(k)));
        ons.notification.alert('Local files have been cleared!');
    },
    onClickBuy:        () => ons.notification.alert('Coming soon!'),
    onClickPack:       async id => {
        document.getElementById("loader").style.display              = 'block';
        document.getElementById("mainmenu-page").style.pointerEvents = 'none';

        let res = await getPackage(id);
        res     = await res.arrayBuffer();

        const loadedZip = await JSZip.loadAsync(res);

        await Promise.all(Object.values(loadedZip.files).map(loadPackFile));

        await root.pushPage('SoundBoardV2', SoundBoardV2.render());

        document.getElementById("loader").style        = '';
        document.getElementById("mainmenu-page").style = '';
    },
};